
img {
    width: 200px;
    height: 200px;
    margin: 3px;
}

img:hover {
    border: 4px blue;
    border-style: outset;
    border-radius: 5px;
    opacity: 0.92;
}

.card {
    position: relative;
}

.card > .imgs {
    float: right;
}

.card > .imgs:first-child {
    float: left;
}